import React from "react";
import { connect } from "react-redux";
import { Scrollbars } from "react-custom-scrollbars";
import ItemListHeader from "./../../ItemsListHeader";
import Search from "./../../UI/Search";
import { ItemEquipmentAvil } from "./../../ItemDetailsRow";
import Skeleton from "react-loading-skeleton";
import GetData from "./../../../servises/getData";
import {
  fetchEquipmentRquest,
  getEquipmentList,
  onSerchInEquipmentsLists,
  addEquipmentToWorker
} from "./../../../actions";
import "./EquipmentListContainer.scss";

const getData = new GetData();

const ItemsLisBox = props => {
  const itemsList = props.list.map(item => (
    <ItemEquipmentAvil
      id={item.id}
      key={item.id}
      image={item.img}
      name={item.name}
      model={""}
      category={item.category}
      assigned={item.assigned}
      available={item.available}
      added={props.isAdd(item.id)}
      onAdd={props.onadd}
    />
  ));
  return itemsList;
};

class EquipmentListContainer extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      hasError: false
    };
  }
  componentDidMount() {
    getData
      .getDataEquipments()
      .then(data => {
        this.props.getEquipmentList(data);
      })
      .then(() => {
        this.props.fetchEquipmentRquest();
      });
  }
  isAssignedEquipCheck = idequip => {
    const { usersList, activeUserId } = this.props;
    const list = usersList[activeUserId].equipment;
    let res = list.filter(e => e.id === idequip).length > 0;
    console.log("item list", res);
    return res;
  };
  addEquipmentToWorker = id => {
    this.props.addEquipmentToWorker(id);
  };
  onSearchInput = val => {
    this.props.onSerchInEquipmentsLists(val);
  };

  render() {
    const { equipmentsList, listsContent } = this.props;

    if (this.state.hasError) {
      return <h1>Something went wrong.</h1>;
    }
    return (
      <div className="ItemsList-wrapp">
        <div className="loader">
          {this.props.isLoading || <Skeleton count={2} />}
          {this.props.isLoading && (
            <Search onSearchInput={this.onSearchInput} />
          )}
        </div>
        {this.props.isLoading || <Skeleton count={5} height={75} />}
        {this.props.isLoading && (
          <div className="items-list">
            <ItemListHeader
              classname="item-row-equipments"
              names={["", "Name", "Category", "Assigned", "Available", ""]}
            />
            <Scrollbars
              style={{ height: 360 }}
              thumbMinSize={50}
              autoHeightMax={100}
              // thumbSize={60}
              // renderThumbVertical={() => <span>---</span>}
              autoHide
              autoHideTimeout={1000}
              autoHideDuration={200}
              className="items-box"
            >
              <ItemsLisBox
                list={equipmentsList}
                isAdd={this.isAssignedEquipCheck}
                onadd={this.addEquipmentToWorker}
              />
            </Scrollbars>
          </div>
        )}
      </div>
    );
  }
}

const mapStateToProps = state => {
  return {
    equipmentsList: state.equipmentsList,
    activeUserId: state.activeUserId,
    usersList: state.usersList,
    listsContent: state.listsContent,
    isLoading: state.isLoading
  };
};

const mapDispatchToProps = {
  fetchEquipmentRquest,
  getEquipmentList,
  onSerchInEquipmentsLists,
  addEquipmentToWorker
};

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(EquipmentListContainer);
