import React, { useState } from "react";
import ModalContainer from "../ModalContainer";
import Button from "./../UI/Button";
import DropDown from "./../UI/DropDown";
import "./App.scss";

function App() {
  const [modalOpen, setModalOpen] = useState(false);

  const onModalOpen = () => {
    setModalOpen(true);
  };
  const onModalClose = () => {
    setModalOpen(false);
  };
  return (
    <div className="app-wrapp app">
      <h1>APP component</h1>
      <div className="btn-wrapp">
        <Button
          className="btn-default"
          label="List of available equipments"
          onClick={onModalOpen}
        />
      </div>
      {modalOpen && (
        <ModalContainer isModalOpen={modalOpen} onClose={onModalClose} />
      )}
    </div>
  );
}

export default App;
