import React, { Component } from "react";
import { connect } from "react-redux";
import { Scrollbars } from "react-custom-scrollbars";
import ItemListHeader from "./../../ItemsListHeader";
import Search from "./../../UI/Search";
import { ItemVehiclesList } from "./../../ItemDetailsRow";

const ItemsLisBox = props => {
  const itemsList = props.list.map(item => (
    <ItemVehiclesList
      id={item.id}
      key={item.id}
      year={item.year}
      image={item.img}
      name={item.name}
      mark={item.mark}
      model={item.model}
      km={item.km}
      licens={item.licens}
      added={props.isAdd(item.id)}
      onAdd={props.onAdd}
    />
  ));
  return itemsList;
};

export class VehiclesContainer extends Component {
  isAssignedVehicleCheck = idVehicle => {
    const { usersList, activeUserId } = this.props;
    let res = usersList[activeUserId].vehicle.includes(idVehicle);
    console.log("item list", res);
    return res;
  };
  onSearchInput = val => {
    this.props.onSerchInEquipmentsLists(val);
  };
  render() {
    return (
      <div className="ItemsList-wrapp">
        <Search onSearchInput={this.onSearchInput} />
        <div className="items-list">
          <ItemListHeader
            classname="item-row-vehicles"
            names={["", "#", "Year", "Model", "KM", "License", " "]}
            VehiclesContainer
          />
          <Scrollbars
            style={{ height: 360 }}
            autoHide
            autoHideTimeout={1000}
            autoHideDuration={200}
            className="items-box"
          >
            <ItemsLisBox
              list={this.props.vehiclesList}
              isAdd={this.isAssignedVehicleCheck}
              onAdd={this.props.addVehicleToWorker}
            />
          </Scrollbars>
        </div>
      </div>
    );
  }
}

const mapStateToProps = state => ({
  activeUserId: state.activeUserId,
  usersList: state.usersList,
  vehiclesList: state.vehiclesList
});

const mapDispatchToProps = dispatch => ({
  addVehicleToWorker: id =>
    dispatch({
      type: "ADD_VEHICLE_TO_WORKER",
      payload: id
    }),
  onSerchInEquipmentsLists: value =>
    dispatch({
      type: "ON_SEARCH_IN_EQUIPMENTS_LIST",
      payload: value
    })
});

export default connect(mapStateToProps, mapDispatchToProps)(VehiclesContainer);
