import React from "react";
import classNames from "classnames";
import "./ItemDetailsRow.scss";

const ItemVehiclesList = ({
  id,
  image,
  year,
  name,
  model,
  mark,
  km,
  licens,
  added,
  onAdd
}) => {
  const onAddHandleClick = e => {
    e.preventDefault();
    onAdd(id);
  };
  let rovClasses = classNames("ItemDetils item-row-vehicles", {
    "item-added": added
  });
  return (
    <div className="ItemDetils-wrapp">
      <div className={rovClasses}>
        <div className="label icon">
          <img src={image} alt="icon" />
        </div>
        <div className="label">{id}</div>
        <div className="label">{year}</div>
        <div className="label">{name}</div>
        <div className="label">{km}</div>
        <div className="label">{licens}</div>
        <div className="label add-btn">
          {added ? (
            <span>Added</span>
          ) : (
            <a href="#" onClick={onAddHandleClick}>
              Add
            </a>
          )}
        </div>
      </div>
    </div>
  );
};

export default ItemVehiclesList;
