import React, { useEffect } from "react";
import { connect } from "react-redux";
import { Scrollbars } from "react-custom-scrollbars";
import ItemListHeader from "./../../ItemsListHeader";
import Search from "./../../UI/Search";
import {
  activeWorkerEquipmentAdd,
  onRemoveItemFromWorker,
  onSerchInWorkerLists
} from "./../../../actions";
import { ItemVechiorActiveList } from "./../../ItemDetailsRow";

const ItemsLisBox = props => {
  const itemsList = props.list.map(item => (
    <ItemVechiorActiveList
      id={item.id}
      key={item.id}
      year={item.year}
      image={item.img}
      name={item.name}
      mark={item.mark}
      model={item.model}
      km={item.km}
      licens={item.licens}
      onRemoveItem={props.onRemoveItem}
    />
  ));
  return itemsList;
};

const WorkerVehiclesListContainer = props => {
  useEffect(() => {
    props.activeWorkerEquipmentAdd();
  }, []);
  const onSearchInput = val => {
    props.onSerchInWorkerLists(val);
  };
  return (
    <div className="ItemsList-wrapp">
      <Search onSearchInput={onSearchInput} />
      <div className="items-list">
        <ItemListHeader
          classname="item-row-vehicles worker-list"
          names={["", "#", "Year", "Model", "KM", "License", ""]}
        />
        <Scrollbars
          style={{ height: 360 }}
          autoHide
          autoHideTimeout={1000}
          autoHideDuration={200}
          className="items-box"
        >
          <ItemsLisBox
            list={props.activeWorkerEquip}
            onRemoveItem={props.onRemoveItemFromWorker}
          />
        </Scrollbars>
      </div>
    </div>
  );
};

const mapStateToProps = state => {
  return {
    activeWorkerEquip: state.activeWorkerEquip,
    activeUserId: state.activeUserId,
    listsContent: state.listsContent
  };
};
const mapDispatchToProps = {
  activeWorkerEquipmentAdd,
  onRemoveItemFromWorker,
  onSerchInWorkerLists
};

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(WorkerVehiclesListContainer);
