import React from "react";
import { connect } from "react-redux";
import { Scrollbars } from "react-custom-scrollbars";
import ItemListHeader from "./../../ItemsListHeader";
import Search from "./../../UI/Search";
import { ItemEquipmentAssign } from "./../../ItemDetailsRow";
import Skeleton from "react-loading-skeleton";
import {
  activeWorkerEquipmentAdd,
  onRemoveEquipmentFromWorker,
  onSerchInWorkerLists,
  getWorkersList
} from "./../../../actions";
import GetData from "./../../../servises/getData";
const getData = new GetData();

class WorkerEquipmentListContainer extends React.Component {
  componentDidMount() {
    getData
      .getDataWorkers()
      .then(data => {
        this.props.getWorkersList(data);
      })
      .then(() => {
        this.props.activeWorkerEquipmentAdd();
        // this.props.fetchEquipmentRquest();
      });
  }
  onSearchInput = val => {
    this.props.onSerchInWorkerLists(val);
  };

  render() {
    return (
      <div className="ItemsList-wrapp">
        <div className="loader">
          {this.props.isLoading || <Skeleton count={2} />}
          {this.props.isLoading && (
            <Search onSearchInput={this.onSearchInput} />
          )}
        </div>
        {this.props.isLoading || <Skeleton count={5} height={75} />}
        {this.props.isLoading && (
          <div className="items-list">
            <ItemListHeader
              classname="item-row-equipments"
              names={["", "Name", "Category", "Assigned", "Available", ""]}
            />
            <Scrollbars
              style={{ height: 360 }}
              autoHide
              autoHideTimeout={1000}
              autoHideDuration={200}
              className="items-box"
            >
              {this.props.activeWorkerEquip.map((i, idx) => {
                const item = i;
                return (
                  <ItemEquipmentAssign
                    key={idx}
                    id={item.id}
                    image={item.img}
                    name={item.name}
                    model={item.model}
                    category={item.category}
                    assigned={item.assigned}
                    available={item.available}
                    onRemoveItem={this.props.onRemoveEquipmentFromWorker}
                  />
                );
              })}
            </Scrollbars>
          </div>
        )}
      </div>
    );
  }
}

const mapStateToProps = state => {
  return {
    activeWorkerEquip: state.activeWorkerEquip,
    activeUserId: state.activeUserId,
    isLoading: state.isLoading
  };
};
const mapDispatchToProps = {
  activeWorkerEquipmentAdd,
  onRemoveEquipmentFromWorker,
  onSerchInWorkerLists,
  getWorkersList
};

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(WorkerEquipmentListContainer);
