import React from "react";
import Select from "react-select";
import "./DropDown.scss";

const customStyles = {
  indicatorSeparator: () => ({
    display: "none"
  }),
  control: (provided, state) => ({
    ...provided,
    border: "none"
  }),
  placeholder: (provided, state) => ({
    ...provided,
    fontFamily: "Ubuntu",
    fontStyle: "normal",
    fontWeight: "normal",
    fontSize: "16px",
    color: "#000000"
  }),
  singleValue: (provided, state) => ({
    ...provided,
    fontFamily: "Ubuntu",
    fontStyle: "normal",
    fontWeight: "normal",
    fontSize: "16px",
    color: "#000000"
  }),
  input: (provided, state) => ({
    ...provided,
    pointerEvents: "none"
  }),
  menu: () => (provided, state) => ({
    ...provided,
    background: "#FFFFFF",
    boxShadow: "0px 2px 10px rgba(22, 84, 152, 0.1)",
    borderRadius: "4px",
    top: "115%",
    position: "absolute",
    padding: "8px 0",
    zIndex: "999"
  }),
  menuList: () => (provided, state) => ({
    ...provided
  }),
  option: (provided, state) => ({
    ...provided,
    border: "none",
    fontFamily: "Ubuntu",
    fontStyle: "normal",
    fontWeight: "normal",
    fontSize: "16px",
    backgroundColor: state.isSelected ? "rgba(197, 215, 255, 0.5);" : "none",
    color: "#14589C"
  })
};
class DropDown extends React.Component {
  state = {
    selectedOption: this.props.defaultValue
  };
  handleChange = selectedOption => {
    this.setState({ selectedOption });
    this.props.onHandleChange(selectedOption);
  };
  render() {
    const { selectedOption, options } = this.state;

    return (
      <div className="select-wrapp">
        <Select
          defaultValue={selectedOption}
          styles={customStyles}
          value={selectedOption}
          onChange={this.handleChange}
          options={this.props.options}
        />
      </div>
    );
  }
}

export default DropDown;
