import React from "react";
import ModalHeader from "./../ModalHeader";
import "./ModalContainer.scss";
import ModalFooter from "./../ModalFooter";
import EquipmentListContainer from "./../containers/EquipmentListContainer";
import WorkerEquipmentListContainer from "./../containers/WorkerEquipmentListContainer";
import VehiclesContainer from "./../containers/VehiclesContainer";
import WorkerVehiclesListContainer from "./../containers/WorkerVehiclesListContainer";
import { connect } from "react-redux";
import classNames from "classnames";


const ModalContainer = props => {
  const modallClasses = classNames("ModalContainerWrapper", {
    show: props.isModalOpen
  });
  const Vehicles = () => (
    <div className="lists-wrapp">
      <div className="left-list">
        <VehiclesContainer />
      </div>
      <div className="right-list">
        <WorkerVehiclesListContainer />
      </div>
    </div>
  );

  const Equipment = () => (
    <div className="lists-wrapp">
      <div className="left-list">
        <EquipmentListContainer />
      </div>
      <div className="right-list">
        <WorkerEquipmentListContainer />
      </div>
    </div>
  );
  return (
    <div className={modallClasses}>     
      <div className="modal-dialog-wrapp">
        <ModalHeader />
        {props.listsContent === "equipment" ? <Equipment /> : null}
        {props.listsContent === "vehicle" ? <Vehicles /> : null}
        <ModalFooter onClose={() => {}} />
      </div>
    </div>
  );
};

const mapStateToProps = state => ({
  listsContent: state.listsContent,
  isLoading: state.isLoading
});

const mapDispatchToProps = {};

export default connect(mapStateToProps, null)(ModalContainer);
