import React, { Component, useEffect, useState } from "react";
import "./UserCard.scss";
import GetData from "./../../servises/getData";
import DropDown from "./../UI/DropDown";
import { connect } from "react-redux";
import Skeleton from "react-loading-skeleton";

const getData = new GetData();

const UserCard = props => {
  const { img, name, id } = props.userDetails;

  const [usersList, setUsersList] = useState();
  const userCgangeHandler = val => {
    props.onUserChange(val.value);
  };
  useEffect(() => {
    const optionsList = Object.keys(props.usersList).map((key, index) => {
      return { label: props.usersList[key].name, value: key };
    });
    setUsersList(optionsList);
  }, [props.usersList]);
  return (
    <div className="user-card-wrapp">
      <div className="avatar">
        <img src={img} alt="avatar" />
      </div>

      <div className="user-name">
        {props.isLoading || <Skeleton count={2} height={20} />}
        {props.isLoading && (
          <DropDown
            options={usersList}
            defaultValue={usersList.find(it => it.value === id.toString())}
            onHandleChange={userCgangeHandler}
          />
        )}
      </div>
    </div>
  );
};

const mapStateToProps = state => {
  const { activeUserId } = state;
  return {
    userDetails: state.usersList[activeUserId],
    usersList: state.usersList,
    isLoading: state.isLoading
  };
};
// const mapDispatchToProps = { onUserIdChange };
const mapDispatchToProps = dispatch => ({
  onUserChange: id =>
    dispatch({
      type: "ON_USER_CHANGE",
      payload: id
    })
});
export default connect(mapStateToProps, mapDispatchToProps)(UserCard);
