import React from "react";
import classNames from "classnames";
import { Xicon } from "./../UI/icons";
import "./ItemDetailsRow.scss";

const ItemVechiorActiveList = ({
  id,
  image,
  year,
  name,
  model,
  mark,
  km,
  licens,
  onRemoveItem
}) => {
  const onRemoveHandleClick = e => {
    e.preventDefault();
    onRemoveItem(id);
  };
  let rovClasses = classNames("ItemDetils item-row-vehicles worker-list");
  return (
    <div className="ItemDetils-wrapp">
      <div className={rovClasses}>
        <div className="label icon">
          <img src={image} alt="icon" />
        </div>
        <div className="label">{id}</div>
        <div className="label">{year}</div>
        <div className="label">{name}</div>
        <div className="label">{km}</div>
        <div className="label">{licens}</div>
        <div className="label delete-btn">
          <a href="#" onClick={onRemoveHandleClick}>
            <Xicon />
          </a>
        </div>
      </div>
    </div>
  );
};

export default ItemVechiorActiveList;
