import React, { Component, useState } from "react";
import "./ModalFooter.scss";
import { connect } from "react-redux";
import Select from "./../UI/Select";
import { changeListsContent } from "./../../actions";

class ModalFooter extends React.Component {
 
  render() {
    return (
      <div className="modal-footer-wrapp">
        <div className="modal-footer">
          <Select
            optopns={[{label:"equipment", value:"equipment"}, {label:"vehicle", value:"vehicle"}]}
            value={this.props.listsContent}
            onHandleChange={this.props.changeListsContent}
          />

          <div className="btn-group-wrapp">
            <button className="cancel-btn" onClick={this.props.onClose}>
              Cancel
            </button>
            <button className="submit-btn">Save</button>
          </div>
        </div>
      </div>
    );
  }
}

const mapStateToProps = state => ({
  listsContent: state.listsContent
});

const mapDispatchToProps = { changeListsContent };

export default connect(mapStateToProps, mapDispatchToProps)(ModalFooter);
