const updataWorkerEquipList = (state, workerId = state.activeUserId) => {
  const workerEquip = state.usersList[workerId].equipment.map(it => {
    let list = state.equipmentsList.filter(e => e.id === it.id);
    return { ...list[0], assigned: it.assigned };
  });
  return workerEquip;
};
const updataWorkerActiveList = (state, workerId = state.activeUserId) => {
  if (state.listsContent === "equipment") {
    const activeList = state.usersList[workerId].equipment.map(it => {
      let list = state.equipmentsList.filter(e => e.id === it.id);
      return { ...list[0], assigned: it.assigned };
    });
    return activeList;
  }
  if (state.listsContent === "vehicle") {
    const activeList = state.usersList[workerId].vehicle.map(it => {
      console.log("//// IT", it);
      let list = state.vehiclesList.filter(e => e.id === it);
      console.log("//// list", list);
      return { ...list[0] };
    });
    return activeList;
  }
};

const updateEquipmentList = (list, item, idx) => {
  console.log("updateEquipmentList", list);
  return [...list.slice(0, idx), item, ...list.slice(idx + 1)];
};

const removeItemFromList = (list, idx) => {
  return [...list.slice(0, idx), ...list.slice(idx + 1)];
};
const updateItem = (item = {}, quantity) => {
  console.log("ITEM ___:", item);
  const { id, name, model, category, img, assigned, available } = item;
  return {
    id,
    name,
    model,
    category,
    img,
    assigned: assigned + quantity,
    available: available - quantity
  };
};
const updateItemVehicle = (item = {}) => {
  const { id, mark, model, name, km, img, license, year } = item;
  return {
    id,
    name,
    mark,
    model,
    km,
    img,
    license,
    year
  };
};
const findItemInList = (list, itid) => {
  const item = list.find(e => e.id === itid);
  return item;
};

const getItemIndexInList = (list, itid) => {
  const idx = list.findIndex(({ id }) => id === itid);
  return idx;
};
// ----------------------------------
const removeAllEqupment = (state, itid) => {
  const { activeUserId, equipmentsList, usersList, activeWorkerEquip } = state;
  // -------------------------------------------------------------------------
  const itemActiv = findItemInList(activeWorkerEquip, itid); // get item in active worker list
  const itemActivIdx = getItemIndexInList(activeWorkerEquip, itid); // get active item in active worker list

  const newActiveWorkerEquip = removeItemFromList(
    activeWorkerEquip,
    itemActivIdx
  ); // create new activeWorkerEquip
  const { assigned, available } = itemActiv; // get counts
  //------------------------------------------------------------------------------
  const activeWorkerItem = usersList[activeUserId];
  const workerEquipmentList = usersList[activeUserId].equipment.filter(
    it => it.id !== itid
  );
  const newactiveWorkerItem = {
    ...activeWorkerItem,
    equipment: workerEquipmentList
  };
  const newusersList = { ...usersList };
  newusersList[activeUserId] = { ...newactiveWorkerItem };

  //--------------------------------------------------------------------------------
  const itemEquip = findItemInList(equipmentsList, itid); // get item in active worker list
  const itemEquipIdx = getItemIndexInList(equipmentsList, itid); // get active item in active worker list
  const newItem = updateItem(itemEquip, -assigned); // get item with new counts
  const newequipmentsList = updateEquipmentList(
    equipmentsList,
    newItem,
    itemEquipIdx
  ); // create new equipmentsList

  return {
    ...state,
    activeWorkerEquip: newActiveWorkerEquip,
    equipmentsList: newequipmentsList,
    usersList: newusersList
  };
};
const removevehicleFromWorker = (state, itid) => {
  const { activeUserId, vehiclesList, usersList, activeWorkerEquip } = state;
  // -------------------------------------------------------------------------
  const itemActivIdx = getItemIndexInList(activeWorkerEquip, itid); // get active item in active worker list

  const newActiveWorkerEquip = removeItemFromList(
    activeWorkerEquip,
    itemActivIdx
  ); // create new activeWorkerEquip

  //------------------------------------------------------------------------------
  const activeWorkerItem = usersList[activeUserId];
  const workerEquipmentList = usersList[activeUserId].vehicle.filter(
    it => it !== itid
  );
  const newactiveWorkerItem = {
    ...activeWorkerItem,
    vehicle: workerEquipmentList
  };
  const newusersList = { ...usersList };
  newusersList[activeUserId] = { ...newactiveWorkerItem };

  return {
    ...state,
    activeWorkerEquip: newActiveWorkerEquip,
    usersList: newusersList,
    vehiclesList: [...vehiclesList]
  };
};
