const changeQuantityWorkerEquipments = (state, id, quantity) => {
  console.log("changeQuantityWorkerEquipments", id);
  console.log("changeQuantityWorkerEquipments", quantity);
  return { ...state };
};
//--------------------------------
const addEquipmentToWorker = (state, equipmentId, quantity) => {
  const equip = findItemInList(state.equipmentsList, equipmentId); // find element by id in equipment list
  const equipIndex = getItemIndexInList(state.equipmentsList, equipmentId); // get equipment element index
  const item = updateItem(equip, quantity); // create new item
  const equipmentsList = updateEquipmentList(
    state.equipmentsList,
    item,
    equipIndex
  ); // create apdated new equipment list
  const { activeUserId } = state; // get activeUserId from state
  const newusersList = { ...state.usersList };
  newusersList[activeUserId].equipment = [
    ...newusersList[activeUserId].equipment,
    { id: equipmentId, assigned: 1 }
  ];
  const usersList = { ...newusersList }; // create new users list with apdated data
  const activeWorkerEquip = [
    ...state.activeWorkerEquip,
    updateItem({ ...equip, assigned: 0 }, 1)
  ]; // create new active worker equipment list

  return { ...state, usersList, equipmentsList, activeWorkerEquip }; // return new state
};

// ---------------------------------------------
const addVehicleToWorker = (state, vehicleId) => {
  const vehicle = findItemInList(state.vehiclesList, vehicleId); // find element by id in vehicles list
  const { activeUserId } = state; // get activeUserId from state
  const newusersList = { ...state.usersList };
  newusersList[activeUserId].vehicle = [
    ...newusersList[activeUserId].vehicle,
    vehicleId
  ];
  const usersList = { ...newusersList }; // create new users list with apdated data
  const activeWorkerEquip = [
    ...state.activeWorkerEquip,
    updateItemVehicle(vehicle)
  ]; // create new active worker equipment list

  return { ...state, usersList, activeWorkerEquip }; // return new state
};
// -----------------------

const onSerchInList = (state, val) => {
  if (state.listsContent === "equipment") {
    const newlist = [...state.equipmentsList];
    let res = newlist.filter(it => new RegExp(val, "i").test(it.name));
    return { ...state, equipmentsList: [...res] };
  }
  if (state.listsContent === "vehicle") {
    const newlist = [...state.vehiclesList];
    let res = newlist.filter(it => new RegExp(val, "i").test(it.name));
    return { ...state, vehiclesList: [...res] };
  }
};
const onSerchInWorkerList = (list, val) => {
  let res = list.filter(it => new RegExp(val, "i").test(it.name));
  return res;
};

//--------------------------------------------------------
const reducer = (state = initialState, action) => {
  console.log(action.type);
  switch (action.type) {
    case "GET_EQUIPMENT_LIST":
      return {
        ...state,
        equipmentsList: action.payload
      };
    case "GET_WORKERS_LIST":
      return {
        ...state,
        usersList: action.payload
      };
    case "FETCH_EQUIPMENT_REQUEST":
      return {
        ...state,
        isLoading: true
      };
    case "ON_USER_CHANGE":
      return {
        ...state,
        activeUserId: action.payload,
        activeWorkerEquip: updataWorkerActiveList(state, action.payload)
      };
    case "CHANGE_LIST_CONTENT":
      return {
        ...state,
        listsContent: action.payload
      };
    case "ADD_ACTIVE_WORKER_EQUIPMENT":
      return {
        ...state,
        activeWorkerEquip: updataWorkerActiveList(state)
      };
    case "ADD_EQUIPMENT_TO_WORKER":
      return addEquipmentToWorker(state, action.payload, 1);

    case "ADD_VEHICLE_TO_WORKER":
      return addVehicleToWorker(state, action.payload);

    case "REMOVE_EQUIPENT_FROM_WORKER_LIST":
      return removeAllEqupment(state, action.payload);

    case "REMOVE_ITEM_FROM_WORKER_LIST":
      return removevehicleFromWorker(state, action.payload);

    case "ON_CHANGE_QUANITY_WORKER_EQUIPMENT":
      return changeQuantityWorkerEquipments(
        state,
        action.payload.id,
        action.payload.quantity
      );
    case "ON_SEARCH_IN_EQUIPMENTS_LIST":
      return onSerchInList(state, action.payload);
    case "ON_SEARCH_IN_WORKER_LIST":
      return {
        ...state,
        activeWorkerEquip: onSerchInWorkerList(
          state.activeWorkerEquip,
          action.payload
        )
      };
    default:
      return state;
  }
};

export default reducer;
