import React from "react";
import "./Select.scss";

const Select = props => {
  return (
    <div className="drop-wrapp">
      <select defaultValue={props.value} onChange={props.onHandleChange}>
        {props.optopns.map((item, idx) => {
          return (
            <option key={idx} value={item.value}>
              {item.label}
            </option>
          );
        })}
      </select>
    </div>
  );
};

export default Select;
