export const getEquipmentList = (data)=>({
  type: "GET_EQUIPMENT_LIST",
  payload: data
})
export const getWorkersList = (data)=>({
  type: "GET_WORKERS_LIST",
  payload: data
})


export const fetchEquipmentRquest =()=>({
  type: "FETCH_EQUIPMENT_REQUEST"
}) 
export const fetch_hes_erroe =(err)=>({
  type: "FETCH_HES_ERROR",
  payload: err
}) 

export const changeListsContent = e => {
  const val = e.target.value;
  return {
    type: "CHANGE_LIST_CONTENT",
    payload: val
  };
};

export const onUserIdChange = e => {
  const val = e.target.value;
  return {
    type: "ON_USER_CHANGE",
    payload: val
  };
};
export const activeWorkerEquipmentAdd = () => {
  return {
    type: "ADD_ACTIVE_WORKER_EQUIPMENT"
  };
};
// ----------------
export const onRemoveEquipmentFromWorker = id => {  
  return {
    type: "REMOVE_EQUIPENT_FROM_WORKER_LIST",
    payload: id
  };
};
//------------------
export const onRemoveItemFromWorker = id => {  
  return {
    type: "REMOVE_ITEM_FROM_WORKER_LIST",
    payload: id
  };
};
export const changeQuantityWorkerEquipments = (args)=>{
    return{
        type:"ON_CHANGE_QUANITY_WORKER_EQUIPMENT",
        payload: args
    }
}

export const onSerchInWorkerLists = (value)=>{
    return {
        type: "ON_SEARCH_IN_WORKER_LIST",       
        payload:value
    }
}

export const  addEquipmentToWorker = (id) =>
({
  type: "ADD_EQUIPMENT_TO_WORKER",
  payload: id
})
export const onSerchInEquipmentsLists = value =>
({
  type: "ON_SEARCH_IN_EQUIPMENTS_LIST",
  payload: value
})

