import React from "react";
import "./ItemDetailsRow.scss";
import classNames from "classnames";
import AvailabelQuanity from "./../UI/AvailabelQuanity";
import { Xicon } from "./../UI/icons";
import { connect } from "react-redux";

const ItemEquipmentAssign = ({
  id,
  image,
  model,
  name,
  category,
  assigned,
  available,
  onRemoveItem
}) => {
  let rovClasses = classNames("ItemDetils item-row-equipments", {
    "item-added": false
  });
  const onRemoveHandleClick = e => {
    e.preventDefault();
    onRemoveItem(id);
  };
  const onQuanityChange = val => {};
  return (
    <div className="ItemDetils-wrapp">
      <div className={rovClasses}>
        <div className="label icon">
          <img src={image} alt="icon" />
        </div>
        <div className="label attrebuts">{name}</div>
        <div className="label category">{category}</div>
        <div className="label assigned">{assigned}</div>
        <div className="label available">{available}</div>
        <div className="label delete-btn">
          <AvailabelQuanity
            maxValue={available}
            onQuanityChange={onQuanityChange}
          />
          <a href="#" onClick={onRemoveHandleClick}>
            <Xicon />
          </a>
        </div>
      </div>
    </div>
  );
};

export default connect()(ItemEquipmentAssign);
